export default {
  props: {
    list: {
      type: Array,
      default: () => {

      },
    },
  },
  data() {
    return {
      selected: 0,

    };
  },
  methods: {
    chooseOne(index) {
      this.selected = index;
      this.$emit('tabEmit', this.selected);
      // this.$router.zyToast(`您选择了${item.title}`);
    },
  },
};



