import ImgsApi from '@/api/imgsApi'
  ;

export default {
  data() {
    return {
      selectedImg: '',
      isMask: false,
      list: [],
    };
  },
  mounted() {
    this.getImgs();
  },
  methods: {
    selectedThis(item) {
      this.selectedImg = item.imgSrc;
      this.isMask = !this.isMask;
    },
    closeMask() {
      this.isMask = !this.isMask;
    },
    getImgs() {
      const data = {
        page: 1,
        size: 100,
      };
      ImgsApi.GetImgs(data).then((res) => {
        this.list = res.data;
      });
    },
  },
};



